import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { Observable } from 'rxjs';
import { SalesK57Col3Component } from 'src/app/sales/components/print/sales/sales-k57-col3/sales-k57-col3.component';
import { SalesK80Col3Component } from 'src/app/sales/components/print/sales/sales-k80-col3/sales-k80-col3.component';
import { SalesK80Col4Component } from 'src/app/sales/components/print/sales/sales-k80-col4/sales-k80-col4.component';
import { SalesA5Component } from 'src/app/sales/components/print/sales/sales-a5/sales-a5.component';
import { SalesA4Component } from 'src/app/sales/components/print/sales/sales-a4/sales-a4.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhReturnShopeeInvoiceAdd, VhReturnShopeeInvoiceDetail, VhReturnShopeeInvoiceDetailCombo } from '../shopee-interface';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'bill-type3-shopee-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  invoice: VhReturnShopeeInvoiceAdd = {
    bill_type: 3,
    payment_type: 2,
    id_customer: '',
    id_branch: '',
    id_employee: '',
    id_wallet: 'id_cash',
    date: '',
    total: 0,
    payment: 0,
    tax: 0,
    discount: 0,
    fee: 0,
    note: '',
    price_type: 0,
    order_number_ecommerce: '',
    id_shop: '',
  }
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore //nhớ dữ liệu truyền từ trang trước
  billShopee: any = this.router.getCurrentNavigation().extras.state.billShopee
  employee: any = {};
  customer: any = {};
  pickerTime = new Date();
  checkPrinter: boolean = false;
  closerBrower: any;
  validProdDetail: boolean = true;//nếu biến này = false tức có sp shopee chưa được liên kết với app
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  invoiceDetail = [];
  subTotal = 0;
  taxValue = 0
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private fncService: FunctionService,
    private nzModalService: NzModalService,
    private cdf: ChangeDetectorRef,
    private vhEcommerce: VhEcommerce
  ) {

  }
  /**Khởi tạo hóa đơn và merge sp của shopee vào billDetail */
  initBill() {
    this.invoice.date = this.pickerTime.toISOString();
    this.invoice.id_employee = this.vhAuth.getUser()._id
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.invoice.id_branch = (this.vhQuerySales.getDefaultBranch()._id)
    this.invoice.id_customer = (this.vhEcommerce.getlocalShopListShopee().find(item => item._id == this.dataRestore.id_shop_selected).data.id_customer)
    this.customer = this.vhQuerySales.getlocalCustomer(this.invoice.id_customer)
    this.invoice.payment_type = (2) //set bill công nợ
    this.addInvoiceDetail(this.billShopee.item_list)
    this.setSubTotal()
    this.invoice.discount = 0
    this.invoice.fee = 0
    this.invoice.order_number_ecommerce = (this.billShopee['order_sn'].toString())
    this.invoice.id_shop = (this.dataRestore['id_shop_selected'])
    this.invoice.note = (`${this.languageService.translate("Order number")} Shopee ${this.billShopee.order_sn}`)
    for (let item of this.invoiceDetail) {
      if (!item.id_product) {
        this.validProdDetail = false;
        break;
      }
    }
  }
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoiceDetail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
    this.setTotal();
  }

  setTotal() {
    this.invoice.total = this.subTotal - this.invoice.discount + this.invoice.fee > 0 ? this.subTotal - this.invoice.discount + this.invoice.fee : 0
    this.cdf.detectChanges()
  }

  ngOnInit(): void {
    

    this.initBill()
    this.eventKeypress()
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
    this.vhAlgorithm.waitingStack().then(() => {
      this.discount_price = this.vhAlgorithm.vhnumeral(".discount-price");
      this.invoice_fee = this.vhAlgorithm.vhnumeral(".invoice-fee")
    })
  }
  event: any // biến lắng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
    window.removeEventListener('beforeunload', this.closerBrower, false)
  }
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113) event.preventDefault();
      switch (keycode) {
        case 112: this.payInvoice(false)
          break;
        case 113: {
          if (this.checkPrinter) this.payInvoice(true)
          break;
        }
        case 114: {
          this.searchProductList = !this.searchProductList;
          break;
        }
      }
    }
    //sự kiện f5, close tab...
    this.closerBrower = (event: BeforeUnloadEvent) => {
      (event || window.event).returnValue = 'Changes you made may not be saved';
      return 'Changes you made may not be saved';
    }
    window.addEventListener('beforeunload', this.closerBrower)
    document.addEventListener('keydown', this.event, false);
  }
  goBack() {
    this.vhComponent.alertConfirm(`${this.languageService.translate("Confirm")}`, "", `${this.languageService.translate("Bạn muốn thoát và không lưu đơn này")}?`, "OK", this.languageService.translate('Cancel'))
      .then(() => { this.isExit = true; this.router.navigate(["/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-receive-goods"], { state: { dataRestore: this.dataRestore } }) }, () => { })
  }
  // -------set employee-------
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee = (event);
    }
  }
  setPickerTime(event) {
    if (event.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 8000)
      this.pickerTime = new Date(this.invoice.date)
      this.invoice.date = (this.pickerTime.toISOString());
    }
    else
      this.vhQuerySales.getValidityDate(new Date(event)).then((validitydate: any) => {
        
        if (validitydate == null) this.invoice.date = (event);
        else {
          this.pickerTime = new Date(this.invoice.date)
          this.invoice.date = (this.pickerTime.toISOString());
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
        }
      })
  }
  discount_price: any;
  editDiscount() {
    if (this.discount_price.getRawValue() && this.discount_price.getRawValue() >= 0) {
      this.invoice.discount = (parseFloat(this.discount_price.getRawValue()));
    } else {
      document.getElementById("discount-price")['value'] = this.invoice.discount;
      this.discount_price = this.vhAlgorithm.vhnumeral('.discount-price');
    }
    this.setTotal()
  }
  invoice_fee: any;
  editFee() {
    if (this.invoice_fee.getRawValue() && this.invoice_fee.getRawValue() >= 0) {
      this.invoice.fee = (parseFloat(this.invoice_fee.getRawValue()));
    } else {
      document.getElementById("invoice-fee")['value'] = this.invoice.fee;
      this.invoice_fee = this.vhAlgorithm.vhnumeral('.invoice-fee');
    }
    this.setTotal()
  }

  compareSubTotal() {
    let sub = this.invoiceDetail.reduce((prev: number, next) => prev + next.quantity * next.price, 0)
    if (sub == this.subTotal) return true
    return false
  }

  getCreateUpdateInvoiceDetail(): VhReturnShopeeInvoiceDetail[] {
    return this.invoiceDetail.map(item => {

      let detail: VhReturnShopeeInvoiceDetail = {
        id_product: item.id_product,
        ptype: item.ptype,
        price: item.price,
        quantity: item.quantity,
        ratio: item.ratio,
      }
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct
      if (item.price < item.price_origin) detail['price_origin'] = item.price_origin
      if (item.price == item.price_origin) detail['price_origin'] = item.price

      if (item.ptype == 5) {
        detail['combos'] = item.combos.map(combo => {
          let product: VhReturnShopeeInvoiceDetailCombo = {
            id_product: combo.id_product,
            price: combo.price,
            quantity: combo.quantity,
            ptype: combo.ptype,
            ratio: combo.ratio ? combo.ratio : 1,
          }
          if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
          if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
          return product;
        })
      }
      return detail

    }).filter(item => item)

  }
  save_enable = true;
  payInvoice(print?) {
    if (this.vhAuth.checkMyPermission("sales_enable_payment")) {
      if (this.compareSubTotal()) {
        this.vhComponent.alertConfirm(
          !print ? `${this.languageService.translate("Pay")}?` : `${this.languageService.translate("Pay & Print")}?`,
          "", "", "OK", this.languageService.translate("Cancel")
        )
          .then(() => {
            if (this.save_enable) {
              this.save_enable = false;
              this.vhComponent.showLoading("", "transparent-loading").then(() => {
                this.createOrUpdate(1).then((bill: any) => {
                  this.save_enable = true;

                  let update = async () => await
                    this.vhEcommerce.updateSalesOrderShopee(this.dataRestore['id_shop_selected'], this.billShopee['order_sn'].toString(), { id_bill_received_goods: bill._id })
                  update()

                  this.vhComponent.hideLoading(0).then(() => {
                    this.vhComponent.showToast(2000,
                      `${this.languageService.translate("Issue invoice")} ${this.languageService.translate("Successfully").toLocaleLowerCase()}`, "success-toast"
                    )
                    if (print) this.handlePrint()
                    else this.handle(true)
                  })
                }).catch(err => { this.vhComponent.hideLoading(0); this.vhComponent.showToast(2000, err) })
              })
            }
          }, () => { })
      } else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("The total price of the product is not equal to the subtotal"))

    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  createOrUpdate(bill_type: 1) {
    return new Promise((resolve, reject) => {
      this.vhQuerySales.createBill_Billdetail({ ...this.invoice }, this.getCreateUpdateInvoiceDetail()).then(res => resolve(res)).catch(err => reject(err))
    })
  }
  /**
  * lấy thông tin dữ liệu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }

  handle(update?: boolean) {
    this.isExit = true;
    if (update) this.dataRestore['update'] = true
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-receive-goods'], { state: { dataRestore: this.dataRestore } })
  }
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle(true))
        })
    }, (err) => this.handle(true))
  }
  // ------thoát trang-----
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Are you sure to cancel the sales order"), "OK", (this.languageService.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }
  /**Phần thêm sản phẩm bổ sung
   * Note : không tính tiền sản phẩm này
   */
  searchProductList: boolean = false;
  closeSearchProductList(data) {
    if (data) {
      if (data.manylot && !data.id_lotproduct) this.openModalChooseLot(data)
      else
        if (data.combos && data.combos.filter(e => e.lots && !e.id_lotproduct).length) {
          this.tempDetail = data;
          this.show_modal_select_lots_combo = true;
        }
        else
          this.addInvoiceDetailExtraEcommerce(data);
    }
    this.searchProductList = false;
  }

  addInvoiceDetailExtraEcommerce(value: any) {

    let index = this.invoiceDetail.findIndex(detail => detail['id_product'] == value['_id'] && detail['free'])
    if (index != -1) {
      if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1
    } else {
      let detail: any = {
        name: value.name,
        id_product: value._id,
        quantity: 1,
        ptype: value.type,
        unit: value.unit,
        units: value.units,
        ratio: value.ratio,
        price: 0,
        price_origin: value.price,
        id_category: value.id_category,
        free: true, //nếu có trường này thì được quyền chỉnh số lượng
        id_subproduct: value.id_subproduct,
        lots: value.lots,
        id_lotproduct: value.id_lotproduct,
      }

      if (value.type == 5) {
        detail['subComboName'] = value.subComboName
        detail['combos'] = [...value.combos.map(item => {
          let product: any = {
            id_product: item.id_product, name: item.name, ptype: item.type,
            price: 0, quantity: item.quantity, id_subproduct: item.id_subproduct,
            lots: item.lots,
            id_lotproduct: item.id_lotproduct, units: item.units, ratio: item.ratio,
            free: true//nếu có trường này thì được quyền chỉnh số lượng
          }
          return product;
        })]
      }

      this.invoiceDetail.unshift(detail)
    }

  }

  checkQuantityProduct(product: VhReturnShopeeInvoiceDetail, event) {
    if (!event) product.quantity = 1;
    else product.quantity = event
    this.setSubTotal();
    this.cdf.detectChanges()
  }
  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhReturnShopeeInvoiceDetail;
  openEditUnitPrice(detail: VhReturnShopeeInvoiceDetail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.isVisibleEditUnitPrice = false;
      this.setSubTotal()
    } else { this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast") }
  }
  deleteGoods(detail: VhReturnShopeeInvoiceDetail) {
    this.vhComponent.alertConfirm("", "", `${this.languageService.translate("Delete")} ${detail.name}?`, "OK", this.languageService.translate("Cancel"))
      .then(() => this.removeInvoiceDetail(detail.id_product), () => { })
  }

  removeInvoiceDetail(id: string) {
    let index = this.invoiceDetail.findIndex(detail => detail['id_product'] == id)
    if (index != -1) {
      this.invoiceDetail.splice(index, 1);
      this.setSubTotal()
    }
  }
  /**
 * Thêm sản phẩm đã bán vào cart
 * Sản phẩm này là sp đã bán trên sàn
 */
  addInvoiceDetail(item_list: Array<any>) {
    let products: Array<any> = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden).filter(item =>  item[this.id_branch] && item[this.id_branch].shopees && item[this.id_branch].shopees.length >= 0)
    for (let item of item_list) {
      let findSuccess: boolean = false
      for (let productOfBranch of products) {
        let findProduct = productOfBranch[this.id_branch].shopees.find(data => data.item_id == item.item_id)
        if (findProduct) {

          if (findProduct.model && findProduct.model.length && item.model_id != 0) {
            let indexModel = findProduct.model.findIndex(model => model.model_id == item.model_id)
            if (indexModel >= 0) {
              this.addInvoiceDetailShopee(item.model_discounted_price || item.model_original_price, item.model_quantity_purchased, productOfBranch, true)
              findSuccess = true
              break;
            }
          } else {
            this.addInvoiceDetailShopee(item.model_discounted_price || item.model_original_price, item.model_quantity_purchased, productOfBranch, true)
            findSuccess = true
            break;
          }
        }
      }
      if (!findSuccess) {
        this.addInvoiceDetailShopee(item.model_discounted_price || item.model_original_price, item.model_quantity_purchased, { name: item.item_name }, false)
        this.validProdDetail = false
      }
    }
  }
  addInvoiceDetailShopee(price, quantity, product, exist) {
    if (exist) {
      this.invoiceDetail.unshift({
        name: product.name,
        id_product: product._id,
        quantity: quantity,
        ptype: product.type,
        unit: product.unit,
        ratio: 1,
        units: product.units,
        price: price,
        price_origin: product.price_origin || null,
        id_subproduct: product.id_subproduct,
        lots: product.lots,
        id_lotproduct: product.id_lotproduct,
        free: false, //nếu có trường này true thì được quyền chỉnh số lượng

      })
    } else {
      this.invoiceDetail.unshift({
        id_product: null,
        quantity: quantity,
        price: price,
        ptype: 1,
        ratio: 1,
        name: product.name
      })
    }
  }

  warning_lot_expried: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').warning_lot_expried
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }

  show_modal_select_lots = false;
  list_search_lot = [];

  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  openModalChooseLot(data) {
    this.tempDetail = data
    this.list_search_lot = this.tempDetail.lots
    if (!this.list_search_lot.length) this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Sản phẩm này không có lô khả dụng để bán!"))
    else this.show_modal_select_lots = true;
  }

  changeLot(e) {
    this.tempDetail.lot_number = this.tempDetail.lots.find(item => item._id == e).lot_number
  }

  handleChooseLot() {
    if (this.warning_lot_expried) {
      let lot_select = this.list_search_lot.find(e => e._id == this.tempDetail.id_lotproduct)
      if (new Date().getTime() >= new Date(lot_select.date_exp).getTime()) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate("Lô này đã hết hạn! Tiếp tục bán sản phẩm này?"),
          nzOnOk: () => {
            this.show_modal_select_lots = false;
            this.tempDetail.lots = this.list_search_lot;
            if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
          },
          nzOnCancel: () => {
            // this.show_modal_select_lots = false;
            // this.tempDetail.lots = this.list_search_lot;
          }

        })
      }
      else {
        this.show_modal_select_lots = false;
        this.tempDetail.lots = this.list_search_lot;
        if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
      }
    }
    else {
      this.show_modal_select_lots = false;
      this.tempDetail.lots = this.list_search_lot;
      if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
    }
  }

  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot
  }

  show_modal_select_lots_combo = false;
  handleChooseLotCombo() {
    if (this.tempDetail.combos.filter(item => item.lots && !item.id_lotproduct).length)
      this.vhComponent.alertMessageDesktop("warning", this.languageService.translate("Vui lòng chọn lô cho sản phẩm thuộc combo này"))
    else {
      this.show_modal_select_lots_combo = false;
      this.closeSearchProductList(this.tempDetail)
    }
  }

}
